import React, {useEffect, useState} from 'react'
import './App.scss'
import StudentsCard from './Components/StudentsCard'


function App() {

  const [students, setStudents] = useState([])
  const [nameSearch, setNameSearch] = useState("")
  const [tagSearch, setTagSearch] = useState("")

  // Fetching students
  useEffect(() => {
      fetch('https://api.hatchways.io/assessment/students')
          .then(response => response.json()
              .then(data => {
                setStudents(data.students)})
          )
          .catch(err => {
              console.log(err)
        })
  }, [])

  // assigning an empty tag array for each student to add tags later
  useEffect(() => {
      let obj = students
      for(let i=0; i<students.length; i++){
          obj[i].Tags=[]
      }
      setStudents(obj)
  }, [students])

  // filter students according to two variables; the following function was written to reduce repetition
  const filter = (a,b) => {
      return a.toLowerCase().includes(b.toLowerCase()) 
  }

  // filtering students in case a tag or a name search was done
  const filteredStudents = students.filter(student =>{
      let fullName = student.firstName + student.lastName // to include both the first and last name
      let Tags = student.Tags
      let joinedTags = Tags && Tags.join(" ")
      return (Tags ? filter(joinedTags,tagSearch) && filter(fullName,nameSearch ) : filter(fullName,nameSearch ))
  })  

  return (
  <div className="App">
        <div className="students_container">
            <div className="search_inputs">
                <input 
                    type="text" 
                    value={nameSearch}
                    onChange={(e) => setNameSearch(e.target.value)} 
                    placeholder="Search by name" 
                    className="student_filtering"
                />
                <input 
                    type="text" 
                    value={tagSearch} 
                    onChange={(e) => setTagSearch(e.target.value)} 
                    placeholder="Search by tag" 
                    className="student_filtering"
                />
            </div>
            {filteredStudents && filteredStudents.map((student,i)=> 
                <StudentsCard student={student} key={i}/>)
            }
        </div>
  </div>
)
}

export default App
